
details {
  width: 40%;
  background: linear-gradient(45deg, #ff0057, #e64a19);
}
details summary {
  padding: 1em;
  outline: none;
  color: white;
  font-size: 2em;
}
details p {
  position: relative;
  margin: 0;
  padding: .5em;
  border-top: medium solid white;
  color: white;
  font-size: 1.5em;
}
details .neon:hover {
  z-index: 2;
}
details .neon {
  position: relative;
  background-color: rgba(0, 0, 0, 0);
}
details .neon::before,
details .neon::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, #ff0057, #e64a19);
  opacity: 0;
  content: "";
  filter: blur(20px);
  z-index: -1;
}
details .neon:hover::before,
details .neon:hover::after {
  transition: .5s;
  opacity: 1;
}
