.p-horizontal_list
{
    display: flex;
    align-items: center;
    background-color: goldenrod;
    border-radius: 10px;
    
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
}

.p-horizontal_item
{
    flex-shrink: 0;
    height: fit-content;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 0 10px 40px -10px rgba(0,0,0,.5);
}

.p-horizontal_card
{
    display: inline-block;
}