.progress-wrapper{
  width: 300px;
  height: 60px;
  top: 50%;
  left: 50%;
  position: absolute;
  transform: translate(-50%, -50%);
}
.progress-bar{
  width: 100%;
  height: 100%;
  transform-style: preserve-3d;
  transform: rotateX(-20deg) rotateY(-30deg);
}
.side{
  width: 100%;
  height:100%;
  background-color: rgb(254, 254, 254, 0.3);
  top: 0;
  left: 0;
  position: absolute;
}
.bottom{
  transform: rotateX(90deg);
  transform-origin: bottom;
  box-shadow: 10px 10px 50px 5px rgba(90, 90, 90, 0.7);
}
.top{
  transform: translate(0, -100%) rotateX(90deg);
  transform-origin: bottom;
}
.back{
  transform: translateZ(-60px);
}
.left{
  width: 60px;
  transform: rotateY(90deg);
  transform-origin: left;
  background-color: rgb(255,0,120,0.6);
}
.bar{
  height: 100%;
  background-color: rgb(255,0,120,0.6);
  box-shadow: 10px 10px 50px 5px rgba(255,0,120,0.3);
  width: 50%;
}
