/* chipアニメーション用設定 */

/* チップアニメーションのベース */
.p-chip_base
{
  position: relative;
  margin: 2rem auto 0;
}

/* チップアニメーションを適用させるテキストにつける */
.p-chip_text
{
  position: absolute;
  margin: 0;
  z-index: 2;
}

.p-chip_box
{
  position: absolute;
  z-index: 1;
}

/* チップアニメーション用 */
.p-chip_color0
{
  background-color: #2d132c;
}

.p-chip_color1
{
  background-color: #801336;
}

.p-chip_color2
{
  background-color: #c72c41;
}

.p-chip_color3
{
  background-color: #ee4540;
}
