/*初期スタイルリセット用*/
body {
    font-family: -apple-system, blinkMacSystemFont, /* Macの欧文はSan Francisco (OS X 10.11以降) */
                 'Helvetica Neue',            /* Macの欧文 (OS X 10.10以前) */
                 'Segoe UI',                  /* Windowsの欧文 */
                 'Hiragino Kaku Gothic ProN', /* Macのヒラギノ */
                 Meiryo,                      /* Windowsのメイリオ */
                 sans-serif;
}
h1,h2,h3,ul
{
    margin: 0;
    padding: 0;
}
li
{
    list-style: none;
}
::-webkit-scrollbar
{
    width: 0;
}