
/* 中央揃え */
.l-centering{
  display: flex;
  justify-content: center;
  align-items: center;
}

/* 左右中央寄せ */
.l-side_spacing{
  width: 80%;
  height: 100%;
  margin-left: auto;
  margin-right: auto;
}

.l-topdown_spacing
{
  margin-top: auto;
  margin-bottom: auto;
}

/* ホーム画面のメイン画面の配置(上下中央よせ　4:6配置)*/
.l-46spacing_wrapper{
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.l-46spacing_conteiner4
{
  flex-grow: 4;
}

.l-46spacing_conteiner6
{
  flex-grow: 6;
}

.l-relative_z1
{
  position: relative;
  z-index: 1;
}

.l-abusolute_z1
{
  position: absolute;
  z-index: 1;
}

.l-abusolute_z2
{
  position: absolute;
  z-index: 2;
}

.l-abusolute_tl0
{
  position: absolute;
  top: 0;
  left: 0;
}