.creativelist{
  margin: 0;
  padding: 0;
  box-shadow: 0 15px 40px rgba(0, 0, 0, 1);
}
.creativelist li{
  position: relative;
  list-style: none;
  font-size: 2em;
  padding: 15px;
  color: #fff;
  font-family: monospace;
  cursor: pointer;
  box-sizing: border-box;
}
.creativelist li:hover{
  color: #111;
  background: #3fff00;
  z-index: 1;
  transform: scaleY(1);
  transition: 0.5s;
  transform-origin: top;
}
.creativelist li:hover::before{
  transform: scaleY(1);
  transition: 0.5s;
  transform-origin: top;
}
.creativelist li::before,
.creativelist li::after{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #3fff00;
  transform: scaleY(0);
  transition: 0.5s;
  transform-origin: bottom;
  content: "";
  filter: blur(20px);
  z-index: -1;
}
.creativelist li:hover::before,
.creativelist li:hover::after{
  transform: scaleY(1);
  transition: 0.5s;
  transform-origin: top;
}
