
.creativebox{
  position: relative;
  width: 280px;
  height: 400px;
  background-color: #060c21;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #000;
}
.creativebox:before{
  content: '';
  position: absolute;
  top: -2px;
  left: -2px;
  right: -2px;
  bottom: -2px;
  background: linear-gradient(315deg, #ff0057, #e64a19);
  transform: skew(2deg, 2deg);
}
.creativebox .content{
  position:relative;
  padding: 20px;
}
.creativebox .content h2{
  position: absolute;
  top: -60px;
  right: 20px;
  margin: 0;
  padding: 0;
  font-size: 10em;
  color: rgba(255, 255, 255, 0.1);
  transition: 0.5s;
}
.creativebox:hover .content h2{
  top: -140px;
}
.creativebox .content h3{
  margin: 0 0 10px;
  padding: 0;
  font-size: 24px;
  font-weight: 500;
  color: white;
}
.creativebox .content p{
  margin: 0;
  padding: 0;
  font-size: 16px;
  color: white;
}
.creativebox .content div{
  position: relative;
  margin: 20px 0 0;
  padding: 10px 20px;
  text-decoration: none;
  border: 1px solid #fff;
  display: inline-block;
  color: #fff;
  transition: 0.5s;
  transform: translateY(-40px);
  opacity: 0;
  visibility: hidden;
}
.creativebox:hover .content div{
  transform: translateY(0px);
  opacity: 1;
  visibility: visible;
}
.creativebox .content div:hover{
  color: #060c21;
  background-color: #fff;
}
